import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
//客户转让
import { AccountManager } from './account-manager/account-manager.component';//客户经理
import { CustomTransfer } from './custom-transfer.component';//客户转让
import { CustomMindTransferComponent } from './account-manager/custom-mind-transfer/custom-mind-transfer.component';//我要转让
import { CustomBatchTransferComponent } from './account-manager/custom-batch-transfer/custom-batch-transfer.component';//客户批量转让
import { CustomMeTransferComponent } from './account-manager/custom-me-transfer/custom-me-transfer.component';//我的转让
import { CustomAlreadyTransferComponent } from "app/pages/tzb/custom/custom-marketing/custom-transfer/account-manager/custom-already-transfer/custom-already-transfer.component";
import { CustomWaitTransferComponent } from "app/pages/tzb/custom/custom-marketing/custom-transfer/account-manager/custom-wait-transfer/custom-wait-transfer.component";
export const routes: Routes = [

    {
        path: '', 
        component: CustomTransfer,
        children: [
            {
                path: 'account-manager',
                component: AccountManager,
                children: [
                    {
                        path: '', redirectTo: 'custom-mind-transfer', component: CustomMindTransferComponent
                    },
                    {
                        path: 'custom-mind-transfer', component: CustomMindTransferComponent
                    },
                    {
                        path: 'custom-batch-transfer', component: CustomBatchTransferComponent
                    },
                    {
                        path: 'custom-me-transfer', component: CustomMeTransferComponent
                    },
                    {
                        path: 'custom-wait-transfer', component: CustomWaitTransferComponent
                    },
                    {
                        path: 'custom-already-transfer', component: CustomAlreadyTransferComponent
                    },
                ]
            },
        ]
    },
]

export const CusTransferRouting: ModuleWithProviders = RouterModule.forChild(routes);
