import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config';
import { WaitTransferListBean } from "./bean/waitTransferList.bean";
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";
import { Message } from 'primeng/components/common/api';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
// import { IsHoldCrdCard } from "app/pages/tzb/custom/constant/custOperation.codeValue";

@Component({
  selector: 'app-wait-transfer-list',
  templateUrl: './wait-transfer-list.component.html',
  styleUrls: ['./wait-transfer-list.component.scss']
})
export class WaitTransferListComponent implements OnInit {

  @Output() outValue = new EventEmitter();
  @Input('in-value') inValue: any;

  tabMesg: any = tableMessage;//表格中无数据时显示的内容
  first: number = 0;//分页控制
  waitTransferListBean: WaitTransferListBean = new WaitTransferListBean();
  tablelist: any = [];
  totalNum: number = 0;
  msgs: Message[] = [];
  isHoldCrdCard: any = [];
  //获取所有码值
  code: any;

  constructor(
    public httpService: CusOperationHttpService,
    public commfunc: Commfunc,
  ) {
    this.codeValues() //调用方法，获取全部码值
    //获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
    this.isHoldCrdCard = this.code['IsHoldCrdCard'];
  }

  //初始化
  ngOnInit() {
  }
  ngOnChanges() {
    this.waitTransferListBean.pageSize = 10;
    this.waitTransferListBean.pageNum = 1;
    this.querySecle();
  }

  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  //查询初始化
  querySecle() {
    this.waitTransferListBean.pageSize = 10;
    this.waitTransferListBean.pageNum = 1;
    this.first = 0;
    this.doQuery();
  }

  //查询
  doQuery() {
    if (this.inValue) {
      this.waitTransferListBean.batchNo = this.inValue.batchNo;
      this.waitTransferListBean.receiver = this.inValue.receiver;
    }
    this.waitTransferListBean.statusId = '1';
    this.waitTransferListBean.operationType = '3';
    this.httpService.queryMainContactChangeByAnyKey(this.waitTransferListBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tablelist = data.resultList;
        this.totalNum = data.total;
        // this.msgs = [];
        // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
    })
  }

  //分页
  paginate(event) {
    this.waitTransferListBean.pageSize = event.rows * 1;
    let currentPage = event.page + 1;
    this.waitTransferListBean.pageNum = currentPage;
    this.first = (currentPage - 1) * this.waitTransferListBean.pageSize;
    this.doQuery();
  }

  //返回关闭模态框
  doClose() {
    this.outValue.emit(false);
  }

  compileStr(cust){
    return this.commfunc.compileStr(cust)
  }
}
