import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { CustomBatchBean } from '../bean/custom-batch.bean'
import { CusOperationHttpService } from '../../../../../http/custom-operation/custom-operation.http.service';
import { TZB_HTTP_URL } from 'app/@uisftech/common/service/http.constant';
import { TZB_CUS_UPLOAD, TZB_CUS_DOWNLOAD } from 'app/@uisftech/common/service/http.constant';
import { Message } from 'primeng/primeng';
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
// import { IsStatusTransfer } from "app/pages/tzb/custom/constant/custOperation.codeValue";

@Component({
  selector: 'app-custom-batch-detail',
  templateUrl: './custom-batch-detail.component.html',
  styleUrls: ['./custom-batch-detail.component.scss'],
  providers: [CusOperationHttpService, Commfunc]
})
export class CustomBatchDetailComponent implements OnChanges {
  tabMesg;
  @Output() outValue = new EventEmitter();
  @Input('in-value') inValue: any;
  tablelist: any = [];
  totalNum: number = 0;
  msgs: Message[] = [];
  param = {};
  isStatus: any = [];
  chooseData: any = [];
  //获取所有码值
  code: any;

  constructor(
    public httpService: CusOperationHttpService,
    public commfunc: Commfunc,
  ) { 
    this.codeValues() //调用方法，获取全部码值
    //获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
    this.isStatus = this.code['IsStatusTransfer'];
  }

  ngOnChanges() {
    if (this.inValue) {
      this.param = { businessId: this.inValue };
      this.doQuery();
    }
  }

  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }

  doQuery() {
    this.httpService.qryProcessByBusId(this.param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tablelist = data.recordList;
        if (this.tablelist) {
          if (this.tablelist.length > 0) {
            this.tablelist.forEach((element, index) => {
              if (element.statusId == null) {
                this.tablelist[index].statusId = '00';
              }
            });
          }
        }
        this.chooseData = this.tablelist;
        this.totalNum = data.totalNum;
        // this.msgs = [];
        // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }

  //关闭
  close() {
    this.outValue.emit(false);
  }

}
