import { Component, OnInit } from '@angular/core';
import { CustomAlreadyTransfer } from './bean/customAlreadyTransfer.bean';
import { CusOperationHttpService } from './../../../../http/custom-operation/custom-operation.http.service';
import { Message } from 'primeng/primeng';
// import { TransferStaus,} from '../../../../constant/custOperation.codeValue';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config';
import { Router } from '@angular/router';
import * as _ from 'lodash';

@Component({
  selector: 'app-custom-already-transfer',
  templateUrl: './custom-already-transfer.component.html',
  styleUrls: ['./custom-already-transfer.component.scss'],
  providers: [CusOperationHttpService, Commfunc]
})
export class CustomAlreadyTransferComponent implements OnInit {

  tabMesg: any = tableMessage;//表格中无数据时显示的内容
  ch: any = CALENDAR_CN;
  customAlreadyTransfer: CustomAlreadyTransfer = new CustomAlreadyTransfer();
  first: number = 0;//分页控制
  totalNum: number = 0;
  tableData: any = [];
  msgs: Message[] = [];		//提示信息
  transferStaus: any = [];//是都有信用卡管道
  startDate: Date = null;
  endDate: Date = null;
  isStatus: any = [];
  statusId_IN: any;
  display: boolean;
  headerTitle: string;
  inValue: any;
  user: any;
  orgId: string = '';
  userId: string = '';
  //获取所有码值
  code: any;

  constructor(
    public httpService: CusOperationHttpService,
    public commfunc: Commfunc,
    public router: Router,
  ) {
    this.codeValues() //调用方法，获取全部码值
    //获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
    this.transferStaus = this.code['TransferStaus'];
    this.isStatus=this.code['IsStatusTransferAlready']
    this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
  }

  ngOnInit() {
    this.customAlreadyTransfer.pageSize = 10;
    this.customAlreadyTransfer.pageNum = 1;
  }

  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }

  //点击查询-初始化
  querySecle() {
    this.customAlreadyTransfer.pageSize = 10;
    this.customAlreadyTransfer.pageNum = 1;
    this.first = 0;
    this.searchBtn();
  }

  // 已转让 查询
  searchBtn() {

    //校验客户号
    if (this.customAlreadyTransfer.custNo == '') {
      this.customAlreadyTransfer.custNo = undefined;
    }
    var reg = /^[0-9]*$/;
    if (this.customAlreadyTransfer.custNo != undefined) {
      if (this.customAlreadyTransfer.custNo != '') {
        if (!reg.test(this.customAlreadyTransfer.custNo)) {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: '请输入正确客户号！' });
          return
        }
      }
    }

    //校验客户姓名
    if (this.customAlreadyTransfer.custName == '') {
      this.customAlreadyTransfer.custName = undefined;
      this.customAlreadyTransfer.custName_LIKE = undefined;
    }
    var reg1 = /^[\u4e00-\u9fa5]{0,}$/;
    if (this.customAlreadyTransfer.custName != undefined) {
      if (this.customAlreadyTransfer.custName != '') {
        if (!reg1.test(this.customAlreadyTransfer.custName)) {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: '请输入正确客户姓名！' });
          return
        }
      }
    }

    //客户姓名有值赋值
    if (this.customAlreadyTransfer.custName != undefined) {
      this.customAlreadyTransfer.custName_LIKE = '%' + this.customAlreadyTransfer.custName + '%';
    }

    //审批状态
    if (!this.customAlreadyTransfer.stu) {
      let arr = [];
      arr.push('0');
      arr.push('1');
      this.customAlreadyTransfer.statusId_IN = arr;
    } else {
      if (this.customAlreadyTransfer.stu == '0') {
        let arr = [];
        arr.push('0');
        this.customAlreadyTransfer.statusId_IN = arr;
      } else {
        let arr = [];
        arr.push('1');
        this.customAlreadyTransfer.statusId_IN = arr;
      }
    }

    this.customAlreadyTransfer.taskCategoryId = '278000';
    this.customAlreadyTransfer.operationType = '3';
    this.customAlreadyTransfer.isAppr = '1';//已审批
    this.customAlreadyTransfer.createDate_GE = this.commfunc.transDateN(this.startDate);
    this.customAlreadyTransfer.createDate_LE = this.commfunc.transDateN(this.endDate);
    this.httpService.queryWaitApprApplyFor(this.customAlreadyTransfer).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tableData = data.resultList;
        this.totalNum = data.total;
        // this.msgs = [];
        // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }

  // 重置
  resetBtn() {
    this.customAlreadyTransfer = new CustomAlreadyTransfer();
    this.customAlreadyTransfer.pageSize = 10;
    this.customAlreadyTransfer.pageNum = 1;
    this.startDate = null;
    this.endDate = null;
    this.first = 0;
  }

  //点击客户号跳转360视图
  toCustView(data) {
    this.commfunc.setSessionData('custNo', data.custNo);
    this.router.navigate(['pages/custom-view']);
  }

  //计划联系时间：开始日期
  showDataStart() {
    if (this.startDate && this.endDate) {
      if (this.startDate > this.endDate) {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
        setTimeout(() => {
          this.startDate = null;
        })
      }
    }
  }
  //计划联系时间：结束日期
  showDataEnd() {
    if (this.startDate && this.endDate) {
      if (this.startDate > this.endDate) {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
        setTimeout(() => {
          this.endDate = null;
        })
      }
    }
  }

  //分页
  paginate(event) {
    this.customAlreadyTransfer.pageSize = event.rows * 1;
    let currentPage = event.page + 1;
    this.customAlreadyTransfer.pageNum = currentPage;
    this.first = (currentPage - 1) * this.customAlreadyTransfer.pageSize;
    this.searchBtn();
  }


  //查看详情
  doDetail(data) {
    this.display = true;
    this.inValue = _.clone(data.batchNo);
    this.headerTitle = '查看详情';
  }

  //查看详情关闭
  doDetailClose() {
    this.display = false;
  }
  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }

  compileStr(cust){
    return this.commfunc.compileStr(cust)
  }
}
