import { Component, OnInit } from '@angular/core';
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Router } from '@angular/router'

@Component({
  selector: 'account-manager',
  templateUrl: './account-manager.component.html',
  styleUrls: ['./account-manager.component.scss'],
  providers: [CusOperationHttpService, Commfunc]
})
export class AccountManager implements OnInit {
  show: number;
  // show: number;
  nav1: boolean = true;
  nav2: boolean = false;
  nav3: boolean = false;
  totalNum: number = 0;
  flag: boolean = false;

  constructor(
    public httpService: CusOperationHttpService,
    public commfunc: Commfunc,
    public router: Router,
  ) { }

  ngOnInit() {

    let stu_1 = this.commfunc.permissionCheck('SID01004_P009');
    let stu_2 = this.commfunc.permissionCheck('SID01004_P010');
    let stu_3 = this.commfunc.permissionCheck('SID01004_P011');
    let stu_4 = this.commfunc.permissionCheck('SID01004_P012');
    let stu_5 = this.commfunc.permissionCheck('SID01004_P013');

    if (stu_1) {
      this.show = 1;
      this.router.navigate(['/pages/tzb/custom/custom-marketing/cus-transfer/account-manager/custom-mind-transfer'])
    } else if (stu_2) {
      this.show = 2;
      this.router.navigate(['/pages/tzb/custom/custom-marketing/cus-transfer/account-manager/custom-batch-transfer'])
    } else if (stu_3) {
      this.show = 3;
      this.router.navigate(['/pages/tzb/custom/custom-marketing/cus-transfer/account-manager/custom-me-transfer'])
    } else if (stu_4) {
      this.show = 4;
      this.router.navigate(['/pages/tzb/custom/custom-marketing/cus-transfer/account-manager/custom-wait-transfer'])
    } else if (stu_5) {
      this.show = 5;
      this.router.navigate(['/pages/tzb/custom/custom-marketing/cus-transfer/account-manager/custom-already-transfer'])
    }

    this.doQuery();
  }
  apply(num) {
    this.show = num;
  }
  //查询
  doQuery() {
    let param = {
      pageSize: 10,
      pageNum: 1,
      operationType: '3',
      taskCategoryId: '278000',
      isAppr: '0',
    }

    this.httpService.queryWaitApprApplyFor(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.totalNum = data.total;

      } else {

      }
    }, error => {

    })
  }
  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }
}
