import { Component, OnInit } from '@angular/core';
import { CusOperationHttpService } from './../../../../http/custom-operation/custom-operation.http.service';
import { Message } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config';
import { CusttomMindTransferBean } from "./bean/custtomMindTransfer.bean";
import { Router } from '@angular/router';
// import { CERT_TYPE, IsHoldCrdCard } from "app/pages/tzb/custom/constant/custOperation.codeValue";

/**
 * 我要转让
 */

@Component({
  selector: 'custom-mind-transfer',
  templateUrl: './custom-mind-transfer.component.html',
  styleUrls: ['./custom-mind-transfer.component.scss'],
  providers: [CusOperationHttpService, Commfunc]
})
export class CustomMindTransferComponent implements OnInit {

  tabMesg: any = tableMessage;//暂无数据
  msgs: Message[] = [];
  custtomMindTransferBean: CusttomMindTransferBean = new CusttomMindTransferBean();
  first: number = 0;//分页控制
  totalNum: number = 0;
  tablelist: any = [];
  cert_type: any = [];
  isHoldCrdCard: any = [];
  chooseData: any = [];
  inValue: any;
  display: boolean = false;
  OwnerDisplay: boolean = false;
  outVal: any;
  aaaa: boolean = false;
  outValBean: any;
  //获取所有码值
  code: any;

  constructor(
    public httpService: CusOperationHttpService,
    public commfunc: Commfunc,
    public router: Router,
  ) {
    this.codeValues() //调用方法，获取全部码值
    //获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
    this.cert_type = this.code['CERT_TYPE'];
    this.isHoldCrdCard = this.code['IsHoldCrdCard'];
  }
  ngOnInit() {
    this.custtomMindTransferBean.pageSize = 10;
    this.custtomMindTransferBean.pageNum = 1;
  }

  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }

  //查询初始化
  querySecle() {
    this.custtomMindTransferBean.pageSize = 10;
    this.custtomMindTransferBean.pageNum = 1;
    this.first = 0;
    this.doQuery();
  }

  //查询
  doQuery() {
    // 是否输入客户号
    if (
      this.custtomMindTransferBean.custNo == undefined ||
      this.custtomMindTransferBean.custNo == ''
    ) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '请输入客户号' });
      return
    }
    // 只允许输入数字
    var reg = /^[0-9]*$/;
    if (this.custtomMindTransferBean.custNo != undefined) {
      if (this.custtomMindTransferBean.custNo != '') {
        if (!reg.test(this.custtomMindTransferBean.custNo)) {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: '只允许输入数字' });
          return
        }
      }
    }
    this.custtomMindTransferBean.operationType = '3';
    this.httpService.distributeCustQry(this.custtomMindTransferBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
    
        this.tablelist = data.custList;
        if (this.tablelist.length > 0) {
          if (this.tablelist[0].ownerManagerName == '' || this.tablelist[0].ownerManagerName == null) {
            this.tablelist[0].ownerManagerName = '-';
            this.OwnerDisplay = false
          } else {
            this.OwnerDisplay = true
          }
          this.chooseData = this.tablelist[0];
        }else{
          this.chooseData=[];
        }
        this.totalNum = data.totalCount;
        // this.msgs = [];
        // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
    })
  }

  //分页
  paginate(event) {
    this.custtomMindTransferBean.pageSize = event.rows * 1;
    let currentPage = event.page + 1;
    this.custtomMindTransferBean.pageNum = currentPage;
    this.first = (currentPage - 1) * this.custtomMindTransferBean.pageSize;
    this.doQuery();
  }

  //点击客户号跳转360视图
  toCustView(data) {
    this.commfunc.setSessionData('custNo', data[0].custNo);
    window.open(`${'localhost:4200' + '#/pages/tzb/custom/custom-information/mine-custom/mine-custom-overview2'}`, '_blank')
  }

  //分配
  doAllot(param) {
    
    document.body.style.overflow = 'hidden';
    if (param == 'more') {
      if (this.chooseData.length == 0) {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '请选择客户！' });
        return
      } else {

        this.inValue = {
          invalue: this.chooseData,
          type: '3'
        }
        this.display = true;
      }
    }
    else {

      this.inValue = {
        invalue: param,
        type: '3'
      }
      this.display = true;
    }

  }


  //多个-分配关闭
  doMoreAllotCall(param) {

    if (param == false) {
      this.tablelist = [];
      this.chooseData = [];
      this.custtomMindTransferBean.custNo = undefined;
      this.display = false;
    } else {
      this.display = false;
      this.outVal = param.outVal;
      this.aaaa = param.aaaa;
    }

  }

  onHideClose() {
    
    document.body.style.overflow = 'auto';
    this.tablelist = [];
    this.chooseData = [];
    this.custtomMindTransferBean.custNo = undefined;
    this.display = false;
  }

  outDisplayCall(event) {
    this.aaaa = event;
    this.display = false;
  }

  outValueCall(event) {
    this.outVal = event;
    let ss = '';
    this.outVal.forEach(item => {
      ss += item.tellerId + ','
    });
    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    commonHeader.extParam['specifyNextOpers'] = ss;
    this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));

    this.httpService.insertCustManageData(this.outValBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (s.extParam.specifyNextOpers) {
          delete s.extParam.specifyNextOpers
          let commonHeader = s;
          this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        }
        this.aaaa = false;
        this.display = false;
        // this.msgs = [];
        // this.msgs.push({ severity: 'success', summary: '提示', detail: '调用服务成功！' });
      } else {
        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (s.extParam.specifyNextOpers) {
          delete s.extParam.specifyNextOpers
          let commonHeader = s;
          this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        }
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
      if (s.extParam.specifyNextOpers) {
        delete s.extParam.specifyNextOpers
        let commonHeader = s;
        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
      }
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    });
  }

  addCallBean(event) {
    this.outValBean = event;
    this.tablelist = []
  }

  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }

  compileStr(cust){
    return this.commfunc.compileStr(cust)
  }
}

