import { Component, OnInit } from '@angular/core';
import { TZB_CUS_UPLOADCQ, TZB_CUS_DOWNLOAD } from 'app/@uisftech/common/service/http.constant';
import { CusOperationHttpService } from '../../../../http/custom-operation/custom-operation.http.service';
import { Message } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Router } from '@angular/router';
import { CustomBatchTransferBean } from "./bean/customBatchTransfer.bean";
// import { CERT_TYPE, IsHoldCrdCard } from "app/pages/tzb/custom/constant/custOperation.codeValue";
import { CustomBatchBean } from "./bean/custom-batch.bean";
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config';

@Component({
  selector: 'app-custom-batch-transfer',
  templateUrl: './custom-batch-transfer.component.html',
  styleUrls: ['./custom-batch-transfer.component.scss'],
  providers: [CusOperationHttpService]
})
export class CustomBatchTransferComponent implements OnInit {

  tabMesg: any = tableMessage;//表格中无数据时显示的内容
  customBatchTransferBean: CustomBatchTransferBean = new CustomBatchTransferBean();
  customBatchBean: CustomBatchBean = new CustomBatchBean();
  first1: number = 0;//分页控制
  first2: number = 0;//分页控制
  tablelist: any = [];
  totalNum: number = 0;
  cert_type: any = [];
  isHoldCrdCard: any = [];
  checkAll: boolean = false;
  checkBox: any = [];
  chooseData: any = [];
  empOrgName: string;

  files: any = [];
  fileList: any = [];//上传文件列表
  fileUrl: string;
  fileName: string;
  DOWNLOAD: string = TZB_CUS_DOWNLOAD;
  UPLOAD: string = TZB_CUS_UPLOADCQ;
  msgs: Message[] = [];           //提示信息
  //模态框的标题
  headerTitle: string = "修改";
  //模态框是否显示
  display: boolean = false;
  orgdisplay: boolean = false;
  showModel: string;
  //参数项
  detailValue: string;
  Invalue: string;
  inValue: any;
  custType: string;
  //显示哪项
  Property1: boolean = true;
  Property2: boolean = false;
  Property3: boolean = false;
  Property4: boolean = false;
  //名单导入
  Propertym: boolean = false;

  startNum: number;
  endNum: number;
  outVal: any;
  aaaa: boolean = false;
  outValBean: any;
  //获取所有码值
  code: any;

  constructor(
    public httpService: CusOperationHttpService,
    public commfunc: Commfunc,
    public router: Router,
  ) {
    this.codeValues() //调用方法，获取全部码值
    //获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
    this.cert_type = this.code['CERT_TYPE'];
    this.isHoldCrdCard = this.code['IsHoldCrdCard'];
  }

  //初始化
  ngOnInit() {

    //默认选中名单导入
    this.custType = '1';
    this.customBatchTransferBean.pageSize = 10;
    this.customBatchTransferBean.pageNum = 1;

    this.customBatchBean.pageSize = 10;
    this.customBatchBean.pageNum = 1;

    this.first1 = 0;
    this.first2 = 0;
  }

  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }

  //上传
  load(event) {

    for (let file of event.files) {
      this.Propertym = false;
      this.tablelist = [];
      this.files = [];
      this.files.push(file)
    }
    if (event.xhr.response) { //上传成功 后台返回的 文件路径 和文件名
      this.fileList = JSON.parse(event.xhr.response);
      if (this.fileList.success == false) {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: this.fileList.message });
        this.files = [];
        return
      }
    }
    if (this.fileList[0]) {
      this.customBatchBean.fileUrl = this.fileList[0].fileUrl;
      this.customBatchBean.fileName = this.fileList[0].fileName;
      this.doQueryList();
    }
  }

  //查询名单导入列表
  doQueryList() {
    this.doClearCheck();
    this.httpService.transferCustBatchQry(this.customBatchBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.Propertym = true;
        this.tablelist = data.batchFileImportInfo;
        this.totalNum = data.totalNum;
        // this.msgs = [];
        // this.msgs.push({ severity: 'success', summary: '提示', detail: '上传成功' });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务异常' });
    })
  }

  //清除全选勾选框+单选框
  doClearCheck() {
    this.chooseData = [];
    this.checkAll = false;
  }

  //查询初始化
  querySecle() {
    this.customBatchTransferBean.pageSize = 10;
    this.customBatchTransferBean.pageNum = 1;
    this.first2 = 0;
    this.doQuery();
  }

  //查询
  doQuery() {
    this.doClearCheck();
    //存款日均校验
    if (this.Property2) {

      var regBalDly = /^[0-9]{0,7}(\.[0-9]{0,7})?$/;
      if (this.customBatchTransferBean.balDlyAvgByYearStart) {
        if (!regBalDly.test(this.customBatchTransferBean.balDlyAvgByYearStart)) {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: '开始存款日均值输入不正确' });
          return
        }
      }

      if (this.customBatchTransferBean.balDlyAvgByYearEnd) {
        if (!regBalDly.test(this.customBatchTransferBean.balDlyAvgByYearEnd)) {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: '结束存款日均值输入不正确' });
          return
        }
      }

      //开始存款日均值不能大于结束存款日均值
      if (this.customBatchTransferBean.balDlyAvgByYearStart && this.customBatchTransferBean.balDlyAvgByYearEnd) {
        this.startNum = parseFloat(this.customBatchTransferBean.balDlyAvgByYearStart);
        this.endNum = parseFloat(this.customBatchTransferBean.balDlyAvgByYearEnd);
        if (this.startNum > this.endNum) {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: '开始存款日均值不能大于结束存款日均值' });
          setTimeout(() => {
            this.customBatchTransferBean.balDlyAvgByYearEnd = '';
          })
          return
        }
      }

    }
    //贷款日均校验
    if (this.Property3) {

      var regBalDly = /^[0-9]{0,7}(\.[0-9]{0,7})?$/;
      if (this.customBatchTransferBean.loanDlyAvgByYearStart) {
        if (!regBalDly.test(this.customBatchTransferBean.loanDlyAvgByYearStart)) {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: '开始贷款日均值输入不正确' });
          return
        }
      }

      if (this.customBatchTransferBean.loanDlyAvgByYearEnd) {
        if (!regBalDly.test(this.customBatchTransferBean.loanDlyAvgByYearEnd)) {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: '结束贷款日均值输入不正确！' });
          return
        }
      }

      //开始贷款日均值不能大于结束贷款日均值
      if (this.customBatchTransferBean.loanDlyAvgByYearStart && this.customBatchTransferBean.loanDlyAvgByYearEnd) {

        this.startNum = parseFloat(this.customBatchTransferBean.loanDlyAvgByYearStart);
        this.endNum = parseFloat(this.customBatchTransferBean.loanDlyAvgByYearEnd);

        if (this.startNum > this.endNum) {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: '开始贷款日均值不能大于结束贷款日均值' });
          setTimeout(() => {
            this.customBatchTransferBean.loanDlyAvgByYearEnd = '';
          })
          return
        }
      }
    }
    this.customBatchTransferBean.operationType = '3';
    this.httpService.distributeCustQry(this.customBatchTransferBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tablelist = data.custList;
        this.totalNum = data.totalCount;
        // this.msgs = [];
        // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
    })
  }

  //重置
  doReset() {
    this.customBatchTransferBean = new CustomBatchTransferBean();
    this.customBatchTransferBean.pageSize = 10;
    this.customBatchTransferBean.pageNum = 1;
    this.first2 = 0;
  }

  //更改查询条件
  changeCustType(param) {
    this.totalNum = 0;
    this.doEmpty();
    this.doReset();
    if (param == '1') {
      this.Property1 = true;
      this.Propertym = false;
      this.display = false;
      if (this.files) {
        this.files = [];
      }
    }
    if (param == '2') {
      this.Property2 = true;
      this.tablelist = [];
    }
    if (param == '3') {
      this.Property3 = true;
      this.tablelist = [];
    }
    if (param == '4') {
      this.Property4 = true;
      this.tablelist = [];
    }
  }

  //清空
  doEmpty() {
    this.Property1 = false;
    this.Property2 = false;
    this.Property3 = false;
    this.Property4 = false;
  }

  //全选按钮
  checkAllClick() {
    if (this.tablelist) {
      for (let i = 0; i < this.tablelist.length; i++) {
        if (this.checkAll == true) {
          this.tablelist[i].checkBox = true;
          this.chooseData.push(this.tablelist[i]);
        }
        else {
          this.chooseData = [];
          this.tablelist[i].checkBox = false;
        }
      }
    }
  }

  //单选按钮
  checkBoxClick(i, data) {
    if (data.checkBox) {
      this.chooseData.push(data)
    } else {
      this.chooseData.forEach((item, i) => {
        if (item == data) {
          this.chooseData.splice(i, 1)
        }
      });
    }
    if (this.chooseData.length == this.tablelist.length) {
      this.checkAll = true;
    } else {
      this.checkAll = false;
    }
  }

  //转让-弹出框
  tran() {
    this.headerTitle = '客户转让';
    this.display = true;
    this.showModel = '2';
    this.Invalue = 'zr';
  }

  //公共参数项
  detail(item) {
    this.headerTitle = '客户批量申领';
    this.display = true;
    this.showModel = '1';
    this.detailValue = item;
  }
  detailCall(param) {
    this.display = param;
    this.msgs = [];
    this.msgs.push({ severity: 'success', summary: '提示', detail: '批量转让成功' });
  }

  //点击客户号跳转360视图
  toCustView(data) {
    this.commfunc.setSessionData('custNo', data.custNo);
    this.router.navigate(['pages/custom-view']);
  }

  //分页
  paginate(event, param) {
    //名单导入
    if (param == 'customBatchBean') {
      this.customBatchBean.pageSize = event.rows * 1;
      let currentPage = event.page + 1;
      this.customBatchBean.pageNum = currentPage;
      this.first1 = (currentPage - 1) * this.customBatchBean.pageSize;
      this.doQueryList();
    }
    //其他
    if (param == 'other') {
      this.customBatchTransferBean.pageSize = event.rows * 1;
      let currentPage = event.page + 1;
      this.customBatchTransferBean.pageNum = currentPage;
      this.first2 = (currentPage - 1) * this.customBatchTransferBean.pageSize;
      this.doQuery();
    }
  }

  //转让
  doAllot(param) {
    if (param == 'more') {
      if (this.chooseData.length == 0) {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '请选择客户！' });
        return
      } else {

        this.inValue = {
          invalue: this.chooseData,
          type: '3'
        }
        this.display = true;
      }
    }
    else {
      this.inValue = {
        invalue: param,
        type: '3'
      }
      this.display = true;
    }
  }

  //转让关闭
  doMoreAllotCall(param) {

    if (param == false) {
      this.tablelist = [];
      this.display = false;
      this.querySecle();
    } else {
      this.display = false;
      this.outVal = param.outVal;
      this.aaaa = param.aaaa;
    }
  }

  //机构树
  orgTree() {
    this.headerTitle = '机构树';
    this.orgdisplay = true;
  }
  treeCall(param) {
    this.orgdisplay = false;
    this.empOrgName = param.orgName;
  }

  //导入转让
  doAllotImport() {
    if (this.tablelist.length > 0) {
      this.inValue = {
        invalue: this.tablelist,
        type: '3'
      }
      this.display = true;
    } else {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '请选择客户！' });
      return
    }
  }

  //多个-分配关闭
  // doMoreAllotCall(param) {
  //   if (param == false) {
  //     this.tablelist = [];
  //     this.custtomMindTransferBean.custNo = undefined;
  //     this.display = false;
  //   } else {
  //     this.display = false;
  //     this.outVal = param.outVal;
  //     this.aaaa = param.aaaa;
  //   }

  // }

  onHideClose() {
    document.body.style.overflow = 'auto';
    // this.tablelist = [];
    this.Propertym = false;
    this.display = false;
    if (this.files) {
      this.files = [];
    }
    this.totalNum = 0;
    this.querySecle();
  }

  outDisplayCall(event) {
    this.aaaa = event;
    this.display = false;
  }

  outValueCall(event) {
    this.outVal = event;
    let ss = '';
    this.outVal.forEach(item => {
      ss += item.tellerId + ','
    });
    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    commonHeader.extParam['specifyNextOpers'] = ss;
    this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));

    this.httpService.insertCustManageData(this.outValBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (s.extParam.specifyNextOpers) {
          delete s.extParam.specifyNextOpers
          let commonHeader = s;
          this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        }
        this.aaaa = false;
        this.display = false;
        // this.msgs = [];
        // this.msgs.push({ severity: 'success', summary: '提示', detail: '调用服务成功！' });
      } else {
        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (s.extParam.specifyNextOpers) {
          delete s.extParam.specifyNextOpers
          let commonHeader = s;
          this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        }
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
      if (s.extParam.specifyNextOpers) {
        delete s.extParam.specifyNextOpers
        let commonHeader = s;
        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
      }
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    });
  }

  addCallBean(event) {
    this.outValBean = event;
  }


  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }

  compileStr(cust){
    return this.commfunc.compileStr(cust)
  }
}
